<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#" :class="{'pagination__link--disabled': currentPage === 1}" @click.prevent="prevPage()" :disabled="currentPage === 1" class="pagination__link pagination__link--arrow" aria-label="Предыдущая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li v-for="page in totalPages" :key="page" class="pagination__item">
      <a href="#" @click.prevent="paginate(page)" class="pagination__link" :class="{'pagination__link--current': page === currentPage}">
        {{ page }}
      </a>
    </li>
    <li class="pagination__item">
      <a @click.prevent="nextPage()" :class="{'pagination__link--disabled': currentPage === totalPages}" :disabled="currentPage === totalPages" class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
    props: ['currentPage', 'count', 'perPage'],
    emits: ['update:currentPage'],
    computed: {
      totalPages() {
        return Math.ceil(this.count / this.perPage);
      }
    },
    methods: {
      paginate(page) {
        this.$emit('update:currentPage', page);
      },
      nextPage() {  
        if (this.currentPage === this.totalPages) return;
        this.$emit('update:currentPage', this.currentPage + 1);
      },
      prevPage() {
        if (this.currentPage === 1) return;
        this.$emit('update:currentPage', this.currentPage - 1);
      },
    }
}
</script>
