<template>
    <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'main'}" class="breadcrumbs__link">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'cart'}" class="breadcrumbs__link">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'order'}" class="breadcrumbs__link">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>

    <section class="cart">
      <form @submit.prevent="order" class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <div class="cart__data">  
            <base-form-text v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя"></base-form-text>
            <base-form-text v-model="formData.address" :error="formError.address" title="Адрес доставки" placeholder="Введите ваш адрес"></base-form-text>
            <base-form-text v-model="formData.phone" :error="formError.phone" title="Телефон" placeholder="Введите ваш телефон"></base-form-text>
            <base-form-text v-model="formData.email" :error="formError.email" title="Email" placeholder="Введите ваш Email"></base-form-text>
            <base-form-text-area v-model="formData.comment" :error="formError.comment" title="Комментарий к заказу" placeholder="Ваши пожелания"></base-form-text-area>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <order-list :products="cartDetailProducts()" />
          <cart-block :amount="cartDetailProducts().length" :price="cartTotalPrice()"/>
          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div v-if="formErrorMessage" class="cart__error form__error-block">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapMutations, mapGetters } from 'vuex';
import BaseFormText from '@/components/BaseFormText';
import BaseFormTextArea from '@/components/BaseFormTextArea';
import OrderList from '@/components/OrderList';
import CartBlock from '@/components/CartBlock';

export default {
    data() {
        return {
            formData: {},
            formError: {},
            formErrorMessage: '',
        }
    },
    components: { BaseFormText, BaseFormTextArea, OrderList, CartBlock },
    methods: {
      ...mapMutations(['resetCart', 'updateOrderInfo']),
      ...mapGetters(['cartDetailProducts', 'cartTotalPrice']),
      order() {
        this.formError = {};
        this.formErrorMessage = '',
        axios
          .post(API_BASE_URL + '/api/orders', {
            ...this.formData,
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            }
          })
          .then(response =>  {
            this.resetCart();
            this.updateOrderInfo(response.data);
            this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
          })
          .catch(error => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
      },
    }
}
</script>
