<template>
    <base-form-field :title="title" :error="error">
        <input class="form__input" :value="modelValue" @input="updateInput" type="text" name="name" :placeholder="placeholder">
    </base-form-field>
</template>

<script>
    import BaseFormField from '@/components/BaseFormField';

    export default {
        props: ['title', 'error', 'placeholder', 'modelValue'],
        components: { BaseFormField },
        methods: {
            updateInput(e) {
                this.$emit('update:modelValue', e.target.value);
            }
        }
    }
</script>
