<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'main'}" class="breadcrumbs__link">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'cart'}" class="breadcrumbs__link">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'order'}" class="breadcrumbs__link">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ 23621</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>
          <order-info-about :information="{...orderInfoDescription}"></order-info-about>
        </div>

        <div class="cart__block">
          <order-list :products="orderProducts"/>
          <cart-block :amount="orderAmount" :price="orderTotalPrice"/>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import OrderList from '@/components/OrderList';
import CartBlock from '@/components/CartBlock';
import OrderInfoAbout from '@/components/OrderInfoAbout'

export default {
  data() {
    return {
      orderProducts: [],
      orderAmount: null,
      orderTotalPrice: null,
      orderInfoDescription: [],
    }
  },
  components: {CartBlock, OrderList, OrderInfoAbout},
    computed: {
      ...mapState(['orderInfo']),
    },
    methods: {
      ...mapActions(['loadOrderInfo'])
    },
    created() {
      if (this.$store.state.orderInfo && this.$store.state.orderInfo === this.$route.params.id) return;
      this.loadOrderInfo(this.$route.params.id).then(() => {
        let {address, comment, email, name, phone} = this.orderInfo
        this.orderProducts = this.orderInfo.basket.items || []
        this.orderAmount = this.orderInfo.basket.items.length || 0
        this.orderTotalPrice = this.orderInfo.totalPrice || 0
        this.orderInfoDescription = {address, comment, email, name, phone}
      })
    }
}
</script>

