<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ countProducts }} товара
      </span>
    </div>
    <div class="content__catalog">
        <product-filter v-model:color="filterColorId" v-model:priceFrom="filterPriceFrom" v-model:priceTo="filterPriceTo" v-model:category="filterCategoryId"></product-filter>
        <section class="catalog">
            <div v-if="productsLoading">Загрузка товаров...</div>
            <div v-if="productsLoadingFailed">Ошибка загрузки товаров  <button @click="loadProducts">Попробовать еще раз</button></div>
            <product-list :products="products"></product-list>
            <base-pagination v-model:currentPage="page" :current-page="page" :count="countProducts" :per-page="productsPerPage"></base-pagination>
        </section>
   </div>
  </main>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config.js'
import ProductList from '@/components/ProductList';
import ProductFilter from '@/components/ProductFilter';
import BasePagination from '@/components/BasePagination'

export default {
    data() {
        return {
            page: 1,
            productsPerPage: 6,
            filterPriceFrom: 0,
            filterPriceTo: 0,
            filterCategoryId: 0,
            filterColorId: 0,
            productsData: null,
            productsLoading: true,
            productsLoadingFailed: false,
        };
    },
    components: { ProductList, ProductFilter, BasePagination, },
    computed: {
        products() {
            return this.productsData
            ? this.productsData.items.map(product => {
              return {
                ...product,
                image: product.image.file.url,
              }
            })
            : [];
        },
        countProducts() {
          return this.productsData ? this.productsData.pagination.total : 0;
        },
    }, 
    methods: {
      loadProducts() {
        this.productsLoading = true;
        this.productsLoadingFailed = false;
        clearTimeout(this.loadProductsTimer);
        this.loadProductsTimer = setTimeout(() => {
          axios
            .get(API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              colorId: this.filterColorId,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
            },
          })
            .then(response => this.productsData = response.data)
            .catch(() => this.productsLoadingFailed = true)
            .then(() => this.productsLoading = false)
        }, 0);
      }
    },
    watch: {
      page() {
        this.loadProducts();
      },
      filterCategoryId() {
        this.loadProducts();
      },
      filterPriceFrom() {
        this.loadProducts();
      },
      filterPriceTo() {
        this.loadProducts();
      },
      filterColorId() {
        this.loadProducts();
      }
    },
    created() {
      this.loadProducts();
    },
};
</script> 
