<template>
  <aside class="filter">
        <h2 class="filter__title">Фильтры</h2>

        <form class="filter__form form" action="#" method="get">
          <fieldset class="form__block">
            <legend class="form__legend">Цена</legend>
            <label class="form__label form__label--price">
              <input v-model.number="currentPriceFrom" class="form__input" type="text" name="min-price">
              <span class="form__value">От</span>
            </label>
            <label class="form__label form__label--price">
              <input v-model.number="currentPriceTo" class="form__input" type="text" name="max-price">
              <span class="form__value">До</span>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Категория</legend>
            <label class="form__label form__label--select">
              <select v-model="currentCategoryId" class="form__select" name="category">
                <option value="0">Все категории</option>
                <option v-for="category in categories" :key="category.id" :value="category.id">{{ category.title }}</option>
              </select>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Цвет</legend>
            <ul class="colors">
              <li v-for="color in colorCategories" :key="color.id" class="colors__item">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" name="color" :value="color.id" v-model="currentColor">
                  <span class="colors__value" :style="{backgroundColor: color.code}">
                  </span>
                </label>
              </li>
            </ul>
          </fieldset>

          <button @click.prevent="submit" class="filter__submit button button--primery" type="submit">
            Применить
          </button>
          <button @click.prevent="reset" class="filter__reset button button--second" type="button">
            Сбросить
          </button>
        </form>
    </aside>
</template>

<script>
import axios from 'axios';
import { mapState } from 'vuex';
import { API_BASE_URL } from '@/config.js'

export default {
    data() {
      return {
        currentPriceFrom: 0,
        currentPriceTo: 0,
        currentCategoryId: 0,
        currentColor: 0,
        categoriesData: null,
        colorCategories: null,
      }
    },
    props: ['priceFrom', 'priceTo', 'category', 'color'],
    emits: ['update:priceFrom', 'update:priceTo', 'update:category', 'update:color'],
    watch: {
        priceFrom(value) {
          this.currentPriceFrom = value;
        },
        priceTo(value) {
          this.currentPriceTo = value;
        },
        categoryId(value) {
          this.currentCategoryId = value;
        },
        color(value) {
          this.currentColor = value;
        }
    },
    computed: {
      categories() {
        return this.categoriesData ? this.categoriesData.items : [];
      }
    },
    methods: {
      ...mapState(['findSimilarCategory']),
      submit() {
        this.$emit('update:priceFrom', this.currentPriceFrom);
        this.$emit('update:priceTo', this.currentPriceTo);
        this.$emit('update:category', this.currentCategoryId);
        this.$emit('update:color', this.currentColor);
      },
      reset() {
        this.$emit('update:priceFrom', 0);
        this.$emit('update:priceTo', 0);
        this.$emit('update:category', 0);
        this.$emit('update:color', 0);
      },
      loadCategories() {
        axios.get(API_BASE_URL + '/api/productCategories')
          .then(response => this.categoriesData = response.data);
      },
      loadColors() {
        axios.get(API_BASE_URL + '/api/colors')
          .then(response => this.colorCategories =  response.data.items)
      },
    }, 
    created() {
      this.loadCategories();
      this.loadColors();
    },
} 
</script>

