<template>
   <li class="catalog__item">
     <router-link :to="{name: 'product', params: {id: product.id}}" class="catalog__pic">
       <img :src="product.image" :alt="product.title">
     </router-link>
     <h3 class="catalog__title">
       <a href="#">
         {{product.title}}
       </a>
     </h3> 
     <span class="catalog__price">
       {{productPrice}} ₽
     </span>

     <ul class="colors colors--black">
       <li v-for="color in product.colors" :key="color.id" class="colors__item">
         <label class="colors__label">
           <input class="colors__radio sr-only" type="radio" :value="color.code" v-model="currentColor">
           <span class="colors__value" :style="{backgroundColor: color.code}">
           </span>
         </label> 
       </li>
     </ul>
   </li>
</template>

<script>
export default {
    data() {
      return {
        currentColor: this.product.colors[0].code,
      }
    },
    props: ['product'],
    computed: {
      productPrice() {
        return new Intl.NumberFormat().format(this.product.price);
      },
    },
    created() {
      console.log()
    }
}
</script>

<style scoped>
  .catalog__pic {
    cursor: pointer;
  }
</style>
