<template>
   <li class="cart__item product">
     <div class="product__pic">
       <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
     </div>
     <h3 class="product__title">
       {{ item.product.title }}
     </h3>
     <span class="product__code">
       Артикул: {{ item.product.id }}
     </span>

     <div class="product__counter form__counter">
       <button @click="decrementAmount(amount)" type="button" aria-label="Убрать один товар">
         <svg width="10" height="10" fill="currentColor">
           <use xlink:href="#icon-minus"></use>
         </svg>
       </button>

       <input type="text" v-model.number="amount" name="count">

       <button @click="incrementAmount(amount)" type="button" aria-label="Добавить один товар">
         <svg width="10" height="10" fill="currentColor">
           <use xlink:href="#icon-plus"></use>
         </svg>
       </button>
     </div>

     <b class="product__price">
       {{ formatPrice(item.product.price * item.amount) }} ₽
     </b>

     <button @click="removeProduct" class="product__del button-del" type="button" aria-label="Удалить товар из корзины">
       <svg width="20" height="20" fill="currentColor">
         <use xlink:href="#icon-close"></use>
       </svg>
     </button>
   </li>
</template>

<script>
import { mapActions } from 'vuex';

export default {
    props: ['item'],
    methods: {
      ...mapActions(['updateCartProductAmount','removeCartProduct']),
      formatPrice(price) {        
        return new Intl.NumberFormat().format(price);
      },
      incrementAmount(value) {
        this.updateCartProductAmount({productId: this.item.productId, amount: value += 1});
      },
      decrementAmount(value) {
        if (value === 1) return;
        this.updateCartProductAmount({productId: this.item.productId, amount: value -= 1});
      },
      removeProduct() {
        this.removeCartProduct(this.item.productId);
      }
    },
    computed: {
        amount: {
            get() {
                return this.item.amount;
            },
            set(value) {
               this.updateCartProductAmount({productId: this.item.productId, amount: value})
            },
        }
    }
}
</script>

<style scoped>
    .button-del {
        cursor: pointer;
    }
</style>