<template>
    <li class="cart__order">
        <h3>{{ product.product.title }}</h3>
        <b>{{ productPrice }} ₽</b>
        <span>Артикул: {{ product.productId || product.product.id }}</span>
    </li>
</template>

<script>
export default {
    props: ['product'],
    computed: {
        productPrice() {
            return new Intl.NumberFormat().format(this.product.product.price);
        },
    }
}
</script>
