<template>
    <base-form-field :title="title" :error="error">
        <textarea class="form__input form__input--area" :value="modelValue" @input="updateInput" name="comments" :placeholder="placeholder"></textarea>
    </base-form-field>
</template>

<script>
    import BaseFormField from '@/components/BaseFormField';

    export default {
        props: ['title', 'error', 'placeholder', 'modelValue'],
        components: { BaseFormField },
        methods: {
            updateInput(e) {
                this.$emit('update:modelValue', e.target.value);
            }
        }
    }
</script>
