<template>
    <div class="cart__total">
        <p>Доставка: <b>500 ₽</b></p>
        <p>Итого: <b>{{ amount }}</b> товара на сумму <b>{{ productPrice }} ₽</b></p>
    </div>
</template>

<script>
export default {
    props: ['amount', 'price'],
    computed: {
        productPrice() {
          return new Intl.NumberFormat().format(this.price);
        },
    },
}
</script>

